import { RendererFactory2 } from '@angular/core';
import { TiRenderer } from '../renderer/TiRenderer';
/**
 * 拖拽配置项参数设置接口
 */
export interface TiDraggableConfig {
    /**
     * 可拖拽元素，仅用于服务生成方式的配置
     */
    helper?: Element;
    /**
     * 拖拽生效元素，即只有当鼠标按下该元素时才可开始拖拽
     */
    handle?: Element;
    /**
     * 可拖拽坐标轴，可定义为x/y，即只可在x和Y方向拖拽
     */
    axis?: 'x' | 'y' | null;
    /**
     * 是否可拖拽，仅用于服务生成方式的配置
     */
    disabled?: boolean;
    /**
     * 拖拽开始事件
     */
    start?(): void;
    /**
     * 拖拽中事件
     */
    drag?(): void;
    /**
     * 拖拽结束事件
     */
    stop?(): void;
}
/**
 * 拖拽服务，适用于可拖拽元素不在html模板中显示声明的情况，除服务方式外，还提供了指令生成方式[TiDraggableDirective]{@link ../directives/TiDraggableDirective.html}，
 * 使用该服务时需要引入模块TiDragServiceModule
 *
 * <example-url>../tiny3demo/#/drag/drag-all</example-url>
 */
export declare class TiDragService {
    private tiRenderer;
    private renderer;
    constructor(rendererFactory: RendererFactory2, tiRenderer: TiRenderer);
    /**
     * 创建拖拽对象
     *
     * 函数返回值为
     * {
     * destroy: () => void
     * }
     */
    create(options: TiDraggableConfig): {
        destroy(): void;
    };
}
